import { Button, Row } from 'antd';
import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

import { API_KEY, API_URL, IMAGE_BASE_URL } from '../Config';
import AntCard from '../commons/AntCard';
import MainImage from './Section/MainImage';


function LandingPage() {
  // useEffect : React        
  //         
  //         
  // =>      

  const navigate = useNavigate();

  // [state]
  const [Movies, setMovies] = useState([]);
  const [MainMovieImage, setMainMovieImage] = useState(null); //    data    ,        null
  const [CurrentPage, setCurrentPage] = useState(0); // 0   1  !

  useEffect(() => {
    const page = 1; //   1      
    fetchMovies(page);
  }, []);

  const loadMoreItems = () => {
    fetchMovies(CurrentPage + 1);
  };

  return (
    <>
      <div>
        {/* <Link to='/items'>items </Link> &nbsp;&nbsp;
        <a href='/items'>[a ] Items  </a> */}
      </div>
      <div style={{ width: '100%' }}>
        {/* Main Image :    */}
        {MainMovieImage &&
          <MainImage
            image={`${IMAGE_BASE_URL}w1280${MainMovieImage.poster_path}`}
            title={`${MainMovieImage.title}`}
            overview={`${MainMovieImage.overview}`}
          />
        }

        {/*   */}
        <div style={{ textAlign: 'center', margin: '20px' }}>
          <Button onClick={() => navigate(1)}></Button>
        </div>

        <div style={{ width: '85%', margin: '1rem auto' }}>
          <h2>  </h2>
          <hr />

          {/* Movie Grid Card :       */}
          <Row gutter={[10, 10]}>
            {/* 6    map     */}
            {Movies.map(movie => {
              return (
                <React.Fragment key={movie.id}>
                  <AntCard
                    landingPage
                    path={`${IMAGE_BASE_URL}w400${movie.poster_path}`}
                    title={movie.title}
                    movieId={movie.id}
                  />
                </React.Fragment>
              );
            })}
          </Row>
        </div>

        {/*   */}
        <div style={{ display: 'flex', justifyContent: 'center', margin: '20px' }}>
          <Button onClick={loadMoreItems}></Button>
        </div>
      </div>
    </>
  )

  function fetchMovies(page) {
    const endpoint = `${API_URL}popular?api_key=${API_KEY}&language=en-US&page=${page}`;

    fetch(endpoint) // 
      .then(res => res.json()) //  : 
      .then(res => {
        // console.log(res.results);
        setMovies([...Movies, ...res.results]); //   :      
        setMainMovieImage(res.results[0]);
        setCurrentPage(res.page);
      });
  }
}

export default LandingPage;