import { Button, Divider } from 'antd';
import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import axios from "axios";

import { API_KEY, API_URL, IMAGE_BASE_URL } from '../Config';
import MainImage from '../LandingPage/Section/MainImage';
import ImgList from './ImgList';
import MovieInfo from './MovieInfo';

const Detail = () => {
  const navigate = useNavigate();
  const { movieId } = useParams();

  // [state]
  const [Movie, setMovie] = useState({}); // json  {} 
  const [Casts, setCasts] = useState([]); //   []  
  const [Crews, setCrews] = useState([]); //   []  
  const [ActorToggle, setActorToggle] = useState(false);
  const [CrewToggle, setCrewToggle] = useState(false);

  useEffect(() => {
    // [  ] URL
    let endpointInfo = `${API_URL}${movieId}?api_key=${API_KEY}`;

    // [/] URL
    let endpointCrew = `${API_URL}${movieId}/credits?api_key=${API_KEY}`;

    // [  ]    
    axios.get(endpointInfo).then(obj => setMovie(obj.data))

    // [/]  
    axios(endpointCrew).then(obj => {
      setCasts(obj.data.cast)
      setCrews(obj.data.crew)
    })

  }, []);

  //   
  function toggleActorView() {
    setActorToggle(!ActorToggle);
  }
  function toggleCrewView() {
    setCrewToggle(!CrewToggle);
  }

  return (
    <>
      {/* Header */}
      {Movie &&
        <MainImage
          image={`${IMAGE_BASE_URL}w1280${Movie.poster_path}`}
          title={`${Movie.title}`}
          overview={`${Movie.overview}`}
        />
      }

      {/*    */}
      <div style={{ textAlign: 'center', margin: '40px' }}>
        {/* -1 :   */}
        <Button onClick={() => navigate(-1)}> </Button>
      </div>

      {/* Body */}
      <div style={{ width: '85%', margin: '20px auto' }}>
        {/* Movie Info */}
        <MovieInfo movie={Movie} />

        <br />

        {/* Actors Grid */}
        {/*    */}
        <div style={{ textAlign: 'center', margin: '40px' }}>
          <div style={{ display: 'inline-block', textAlign: 'center', margin: '5px' }}>
            <Button type={ActorToggle ? 'primary' : 'dashed'} onClick={toggleActorView}> </Button>
          </div>
          <div style={{ display: 'inline-block', textAlign: 'center', margin: '5px' }}>
            <Button type={CrewToggle ? 'primary' : 'dashed'} onClick={toggleCrewView}> </Button>
          </div>
        </div>

        {/*    */}
        {ActorToggle &&
          <>
            <Divider style={{ borderColor: '#00f' }}> </Divider>
            <ImgList 
              list={Casts}
            />
            {/* {<Row gutter={[10, 10]}>
              {Casts.map(cast => {
                return (
                  <React.Fragment key={cast.id}>
                    {cast.profile_path &&
                      <AntCard
                        path={`${IMAGE_BASE_URL}w400${cast.profile_path}`}
                        castName={cast.name}
                      />
                    }
                  </React.Fragment>
                );
              })}
            </Row>} */}
          </>
        }

        {/*    */}
        {CrewToggle &&
          <>
            <Divider style={{ borderColor: '#00f' }}> </Divider>
            <ImgList 
              list={Crews}
            />
            {/* {<Row gutter={[10, 10]}>
              {Crews.map((crew, index) => {
                return (
                  <React.Fragment key={index}>
                    {crew.profile_path &&
                      <AntCard
                        path={`${IMAGE_BASE_URL}w400${crew.profile_path}`}
                        castName={crew.name}
                      />
                    }
                  </React.Fragment>
                );
              })}
            </Row>} */}
          </>
        }

      </div>
    </>
  );
};

export default Detail;