import React from 'react';
import { Layout, Menu } from 'antd';

const { Header } = Layout;

//        
// const items1 = ['HOME', ''].map((key, test) => ({
//   key,
//   label: <a href={
//     key == 'HOME' ? '/' : key
//   }>{key}</a>,
// }));

//      
const items = [{
  key: 'Home',
  label: (<a href='/movie'>Home</a>)
}];

const NavBar = () => {
  return (
    <Layout>
      <Header
        style={{ display: 'flex', alignItems: 'center', }}>
        <div className="demo-logo" />
        <Menu
          theme="dark"
          mode="horizontal"
          defaultSelectedKeys={['Home']}
          items={items}
          style={{
            flex: 1,
            minWidth: 0,
          }}
        />
      </Header>
    </Layout>
  );
};

export default NavBar;